import React from 'react';
import '../styles/Page.css';
import {Route, Switch} from 'react-router-dom'
import StartPage from '../pages/StartPage'
import Books from '../pages/Books'
import Author from '../pages/Author'
import Movies from '../pages/Movies'
import Games from '../pages/Games'
import ErrorPage from '../pages/ErrorPage'
import Contact from '../pages/Contact'
import Sword from '../pages/Sword'
import Last from '../pages/Last'
import EndBegin from '../pages/SomethingEndsBegins'
import BloodOfElves from '../pages/BloodOfElves'
import WitcherStory from '../pages/WitcherStory'



const Page = () => {
    return ( 
        <Switch>
            <Route path="/" exact={true} component={StartPage}/>
            <Route path="/author" component={Author}/>
            <Route path="/books" component={Books}/>
            <Route path="/movies" component={Movies}/>
            <Route path="/games" component={Games}/>
            <Route path="/blood-of-elves" component={BloodOfElves}/>
            <Route path="/contact" component={Contact}/>
            <Route path="/something-ends-something-begins" component={EndBegin}/>
            <Route path="/the-last-wish" component={Last}/>
            <Route path="/sword-of-destiny" component={Sword}/>
            <Route path="/witcher-story" component={WitcherStory}/>
            <Route component={ErrorPage}/>
        </Switch>
     );
}
 
export default Page;