import React from 'react';
import {NavLink} from 'react-router-dom';
import '../styles/Books.css';

const storyList = [
    {name: "Wiedźmin", year: 1986, path: "/witcher-story"},
    {name: "Miecz przeznaczenia", year: 1992, path: "/sword-of-destiny"},
    {name: "Ostatnie życzenie", year: 1993, path: "/the-last-wish"},
    {name: "Coś się kończy, coś się zaczyna", year: 2000, path: "/something-ends-something-begins"},
]

const bookList = [
    {name: "Krew elfów", year:1994, path: "/blood-of-elves"},
    {name: "Czas pogardy", year: 1995, path: "/time-of-contempt"},
    {name: "Chrzest ognia", year: 1996, path: "/baptism-of-fire"},
    {name: "Wieża Jaskółki", year: 1997, path: "/the-tower-of-the-swallow"},
    {name: "Pani Jeziora", year: 1999, path: "/the-lady-of-the-lake"},
]

const Books = () => {
    const style = {textIndent: "0"};

    const stories = storyList.map(story => (
        <NavLink exact={true} to={story.path}><li key={story.name}>{story.name} - {story.year}</li></NavLink>
    ))

    const books = bookList.map(book => (
        <NavLink to={book.path}><li key={book.name}>{book.name} - {book.year}</li></NavLink>
    ))

    return ( 
        <div className="description">
            <h1>Opowiadania oraz saga</h1>
            <article style={style}>
                <p>Opowiadania</p>
                <ul className="stories">
                    {stories}
                </ul>
                <p>Książki</p>
                <ul className="books">
                    {books}
                </ul>
            </article>
        </div>
     );
}
 
export default Books;