import React from 'react';


const WitcherStory = () => {
    return ( 
        <div className="description">
            <h1>Wiedźmin</h1>
            <article>
            Zbiór opowiadań, książkowy debiut Andrzeja Sapkowskiego. Zawiera pięć opowiadań o wiedźminie Geralcie opublikowanych w latach 1986–1990 w czasopiśmie „Fantastyka”. Książka została opublikowana nakładem wydawnictwa Reporter w roku 1990, w nakładzie 30 tys. egzemplarzy.
            </article>
        </div>
     );
}
 
export default WitcherStory;