import React from 'react';


const Author = () => {
    return ( 
        <div className="description">
            <h1>O autorze</h1>
            <article>
                Andrzej Sapkowski ur. 21.06.1948r w Łodzi - polski pisarz fantasy, z wykształcenia ekonomista. Twórca postaci wiedźmina. Jest najczęściej po Lemie tłumaczonym autorem fantastyki. 
                Urodził się i mieszka w Łodzi. Od 9 lipca 2008 roku jest honorowym obywatelem tego miasta. Jest absolwentem XXI Liceum Ogólnokształcącego im.Bolesława Prusa w Łodzi oraz Uniwersytetu Łódzkiego. Początkowo pracował w handlu zagranicznym - handlował futrami. Kariere literacką zaczynał jako tłumacz, przekładając na język polski oppowiadanie Słowa Guru Cyryla M.Kornblutha. Popularność zdobył cyklem opowiadań i pięciotomową sagą o wiedźminie- pierwsze opowiadanie "Wiedźmin", ukazało się w grudniu 1986 w miesięczniku "Fantastyka" jako laureat III nagrody w konkursie literackim ogłoszonym przez czasopismo. Oprócz tego wydał esej o Rycerzach Okrągłego Stołu Świat króla Artura. Maladie, leksykon miłośnika fantasy Rękopis znaleziony w Smoczej Jaskini, poradnik dla osób zaczynających grać w RPG pt. Oko Yrrhedesa, powieści z cyklu o Reinmarze z Bielawy (Trylogia husycka), powieść Żmija oraz wiele opowiadań i artykułów w czasopismach i zbiorach opowiadań.
            </article>
        </div>
     );
}
 
export default Author;